@import url(https://fonts.googleapis.com/css2?family=Share:wght@700&display=swap);
@import url(https://fonts.googleapis.com/css2?family=Share:wght@700&display=swap);
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

@charset "UTF-8";
/* font-family: 'Share', cursive, monospace; */

/* ==================================================================================== */

:root {
  /* Font Sizes */
  --base-font-size: 24px;   /* 1rem */

  /* Widths */

  /* Margins & Paddings */
  --padding-base: 1rem;

  /* Radius */
  --border-radius: 2px;

  /* Colors */
  --color-active            :#1AC1D3;   /* rgba(26, 193, 211, 1) */
  --color-recovered         :#83C489;   /* rgba(131, 196, 137, 1) */
  --color-vaccinated        :#F2C36D;   /* rgba(242, 195, 109, 1) */
  --color-confirmed         :#DA3F2F;   /* rgba(218, 63, 47, 1) */
  --color-deceased          :#595959;   /* rgba(89, 89, 89, 1) */
  --color-neutral           :#6DA8F2;   /* rgba(109, 168, 242, 1) */

  --color-active-light      :rgba(26, 193, 211, 0.1);
  --color-recovered-light   :rgba(131, 196, 137, 0.1);
  --color-vaccinated-light  :rgba(242, 195, 109, 0.1);
  --color-confirmed-light   :rgba(218, 63, 47, 0.1);
  --color-deceased-light    :rgba(89, 89, 89, 0.1);
  --color-neutral-light     :rgba(109, 168, 242, 0.1);


  /* Extra */
  --animation-duration: 180s;
}

body { font-family: 'Share', monospace; }

#topcards .col {
  padding: 1rem;
  padding: var(--padding-base);
  border-radius: 2px;
  border-radius: var(--border-radius);
  cursor: pointer;
}

#topcards .col:nth-of-type(1) { background: rgba(218, 63, 47, 0.1); background: var(--color-confirmed-light); }
#topcards .col:nth-of-type(2) { background: rgba(26, 193, 211, 0.1); background: var(--color-active-light); }
#topcards .col:nth-of-type(3) { background: rgba(131, 196, 137, 0.1); background: var(--color-recovered-light); }
#topcards .col:nth-of-type(4) { background: rgba(89, 89, 89, 0.1); background: var(--color-deceased-light); }
#topcards .col:nth-of-type(5) { background: rgba(242, 195, 109, 0.1); background: var(--color-vaccinated-light); }

#topcards .col:nth-of-type(1):hover { background: #DA3F2F; background: var(--color-confirmed); color: #fff; }
#topcards .col:nth-of-type(2):hover { background: #1AC1D3; background: var(--color-active); color: #fff; }
#topcards .col:nth-of-type(3):hover { background: #83C489; background: var(--color-recovered); color: #fff; }
#topcards .col:nth-of-type(4):hover { background: #595959; background: var(--color-deceased); color: #fff; }
#topcards .col:nth-of-type(5):hover { background: #F2C36D; background: var(--color-vaccinated); color: #fff; }

#topcards #confirmed.selected   { background: #DA3F2F; background: var(--color-confirmed); color: #fff; }
#topcards #active.selected      { background: #1AC1D3; background: var(--color-active); color: #fff; }
#topcards #recovered.selected   { background: #83C489; background: var(--color-recovered); color: #fff; }
#topcards #deceased.selected    { background: #595959; background: var(--color-deceased); color: #fff; }
#topcards #vaccination.selected { background: #F2C36D; background: var(--color-vaccinated); color: #fff; }

.delta-up::before   { content: '\2191 '; font-size: 1.5rem; }
.delta-down::before { content: '\2193 '; font-size: 1.5rem; }

circle.confirmed           { fill: #DA3F2F; fill: var(--color-confirmed); }
circle.active              { fill: #1AC1D3; fill: var(--color-active); }
circle.recovered           { fill: #83C489; fill: var(--color-recovered); }
circle.deceased            { fill: #595959; fill: var(--color-deceased); }
circle.vaccination         { fill: #F2C36D; fill: var(--color-vaccinated); }

.last-updated { color: #6DA8F2; color: var(--color-neutral); }

/* SVGs */
svg { overflow: visible; }

path.active, h2.active, p.active              { fill: var(--color-active); color: var(--color-active); }
path.recovered, h2.recovered, p.recovered     { fill: var(--color-recovered); color: var(--color-recovered); }
path.vaccinated, h2.vaccinated, p.vaccinated  { fill: var(--color-vaccinated); color: var(--color-vaccinated); }
path.confirmed, h2.confirmed, p.confirmed     { fill: var(--color-confirmed); color: var(--color-confirmed); }
path.deceased, h2.deceased, p.deceased        { fill: var(--color-deceased); color: var(--color-deceased); }

path.active     { stroke: var(--color-active); }
path.recovered  { stroke: var(--color-recovered); }
path.vaccinated { stroke: var(--color-vaccinated); }
path.confirmed  { stroke: var(--color-confirmed); }
path.deceased   { stroke: var(--color-deceased); }

path.pathstroke { fill: none; stroke-width: 2; }

h2.title { font-size: 2.5rem; }

p.highlighted { font-size: 1.5rem; }

.subtitle text {
  text-decoration: underline;
  font-size: 12px;
}

.axis .domain,
.axis.xAxis line {
  display: none;
}

.axis.yAxis line {
  stroke-width: 0;
}
